.form {
  background: white;
  padding: 10px;
  display: grid;
  grid-template-columns: 1fr auto;
  grid-gap: 10px;
}

@media (max-width: 767px) {
  .form {
    grid-template-columns: 1fr;
  }
}

.form__input {
  padding: 10px;
  border: 1px solid #ddd;
}

.form__button {
  background-color: teal;
  cursor: pointer;
  color: white;
  border: none;
  padding: 10px 10px 10px 10px;
  transition: 0.5s;
}

.form__button:hover {
  background: hsl(180, 100%, 35%);
  transform: scale(1.1);
}

.form__button:active {
  border: 2px solid black;
  border-radius: 2px;
}
